<style>
  .deli-order-detail-wrap {
    flex: 1;
    background-color: #fff;
  }
  .order-log-item-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .log-cre-t-w {
    flex: 0 0 160px;
    width: 160px;
    padding: 0 5px;
    box-sizing: border-box;
  }
  .log-mesg-w {
    flex: 1;
    padding: 0 5px; 
  }
  .deli-order-detail-wrap>ul>li {
    padding: 10px;
    border-left: 1px solid #ccc;
    position: relative;
  }
  .deli-order-detail-wrap>ul>li::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    left: -4px;
    top: 50%;
    margin-top: -4px;
    background-color: #fff;
    border-radius: 50%;
    border: 1px solid #ccc;
  }
  
  .first-deli-str {
    color: #67C23A;
  }
  .deli-company-name {
    color: #E6A23C;
  }

</style>

<template>
  <div>
     <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="下单时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRange"
            @datePickerRangeChange="datePickerRangeChange">
          </date-picker-range>
        </el-form-item>

        <el-form-item label="送达时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRangeDelivery"
            @datePickerRangeChange="datePickerRangeChangeDelivery">
          </date-picker-range>
        </el-form-item>

        <el-form-item label="配送公司">
          <el-select class="search-input" v-model="searchContent.logisticsCompanyCode" filterable placeholder="请选择配送公司" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in logisticsCompanyCodeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="订单号">
          <el-input class="search-input" v-model.trim="searchContent.orderId" placeholder="请输入订单号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>

        <el-form-item label="配送状态">
          <el-select class="search-input" v-model="searchContent.deliveryStatus" filterable placeholder="请选择配送状态" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in deliveryStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="订单状态">
          <el-select class="search-input" v-model="searchContent.orderStatus" filterable placeholder="请选择配送状态" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in orderStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        
        <!-- <el-form-item label="仅查看本城市">
          <el-select class="search-input" v-model="searchContent.currentCity" placeholder="请选择" size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in currentCityOptions"></el-option>
          </el-select>
        </el-form-item> -->

        <!-- <el-form-item label="门店">
          <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择门店" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item> -->

        <region-city-store @StoreGroupChange="StoreGroupChange" :showGroup="false"></region-city-store>
        
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          <el-button :icon="exportLoading? 'el-icon-loading' : 'el-icon-download'" @click="exportHandel" size="mini">导出</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="70px" prop="cityName" label="城市"></el-table-column>
        <el-table-column min-width="100px" prop="storeName" label="门店"></el-table-column>
        <el-table-column min-width="178px" prop="orderId" label="订单号">
          <template slot-scope="scope" size="small">
            <div class="order-id">
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderId}}</router-link>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="170px" prop="expectedTime" label="期望送达时间"></el-table-column>
        <el-table-column min-width="90px" prop="logisticsCompany" label="配送公司"></el-table-column>
        <el-table-column min-width="80px" prop="deliveryStatusStr" label="状态"></el-table-column>
        <el-table-column min-width="80px" prop="orderStatusStr" label="订单状态"></el-table-column>
        <el-table-column min-width="170px" prop="orderCreateTime" label="下单时间"></el-table-column>
        <el-table-column min-width="170px" prop="deliveryTime" label="派单时间"></el-table-column>
        <el-table-column min-width="170px" prop="shipperAllocatedTime" label="分配骑手时间"></el-table-column>
        <el-table-column min-width="170px" prop="shipperReceivedTime" label="揽件时间"></el-table-column>
        <el-table-column min-width="170px" prop="shipperDeliveredTime" label="送达/取消配送时间"></el-table-column>
        <el-table-column
          align="left"
          fixed="right"
          label="操作"
          width="120">
          <template slot-scope="scope" size="small">
            <div>  
              <el-button @click="showItem(scope.row)" type="text" size="mini" :icon="iconLoading && scope.row.orderId === orderNo ? 'el-icon-loading' : 'el-icon-view'">查看配送日志</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <el-dialog title="查看配送日志" :visible.sync="dialogVisible" width='460px'>
      <div class="deli-order-detail-wrap">
        <ul>
          <li v-for="(item, index) of orderDeliveryLogQuery" class="order-log-item-wrap" :key="index">
            <div class="log-cre-t-w">{{item.createTime}}</div>
            <div class="log-mesg-w">
              <div class="log-s-w" :class="index === 0 ? 'first-deli-str' : ''">
                <span>{{item.deliveryStatusStr}}</span>
                <span class="deli-company-name">{{item.logisticsCompany}}</span> 
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="mini">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import Storage from '@/utils/localStorage'
  import config from '@/utils/config'
  import exportSheet from '@/utils/exportExcel'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';
  const reqUrls = {
    // 获取门店列表接口
    // search: '/plateform/a/store/condition/search',
    // orderDeliveryQuery 配送单查询
    orderDeliveryQuery: '/plateform/a/logistics/orderDeliveryQuery',
    // orderDeliveryLogQuery 配送单日志
    orderDeliveryLogQuery: '/plateform/a/logistics/orderDeliveryLogQuery'
  }
  export default {
    data() {
      return {
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        pageSizes: config.pageSizes20,
        searchContent: {
          DIFFCITY: true,
          defaultRange: [],
          beginTime: '', // 开始时间
          endTime: '', // 结束时间
          defaultRangeDelivery: [],
          deliveryBeginTime: '', // 送达开始时间
          deliveryEndTime: '', // 送达结束时间
          logisticsCompanyCode: '', // 配送公司:1001-自配送;1002-美团;1003-达达
          deliveryStatus: '', // 配送状态: 0-待派单；1-已接单；2-已分配骑手；3-骑手已揽件；4-已送达；9-配送失败；10-已取消
          currentCity: false, // 是否本城市
          storeNo: '', // 门店
          regionCode: Storage.get('regionCode') || ''
        },
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],

        orderNo: '', // 订单号
        iconLoading: false, // 点击的当条信息
        dialogVisible: false, // 配送日志信息弹框是否显示
        orderDeliveryLogQuery: [], // 配送日志信息

        logisticsCompanyCodeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '美团配送',
            value: 1002
          },
          {
            label: '达达配送',
            value: 1003
          },
          {
            label: '门店自配送',
            value: 1001
          },
          {
            label: '顺丰同城配送',
            value: 1004
          }
        ],
        // 配送状态: 0-待派单；1-已接单；2-已分配骑手；3-骑手已揽件；4-已送达；9-配送失败；10-已取消
        deliveryStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '待派单',
            value: 0
          },
          {
            label: '已接单',
            value: 1
          },
          {
            label: '已分配骑手',
            value: 2
          },
          {
            label: '骑手已揽件',
            value: 3
          },
          {
            label: '已送达',
            value: 4
          },
          {
            label: '配送失败',
            value: 9
          },
          {
            label: '已取消',
            value: 10
          }
        ],
        // 订单状态：1-待出票；2-待备货；3-待揽件；5-配送中；6-已完成；7-已取消；10-已退款；11-已失效
        orderStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '待出票',
            value: 1
          },
          {
            label: '待备货',
            value: 2
          },
          {
            label: '待揽件',
            value: 3
          },
          {
            label: '配送中',
            value: 5
          },
          {
            label: '已完成',
            value: 6
          },
          {
            label: '已取消',
            value: 7
          },
          {
            label: '已退款',
            value: 10
          },
          {
            label: '已失效',
            value: 11
          }
        ],
        // 仅查看本城市可售 选择
        currentCityOptions: [
          {
            label: '是',
            value: true
          },
          {
            label: '否',
            value: false
          }
        ],
        exportLoading: false,
        exportConfig: {
          cityName: '城市',
          storeName: '门店',
          orderId: '订单号',
          expectedTime: '期望送达时间',
          logisticsCompany: '配送公司',
          deliveryStatusStr: '状态',
          orderStatusStr: '订单状态',
          orderCreateTime: '下单时间',
          deliveryTime: '派单时间',
          shipperAllocatedTime: '分配骑手时间',
          shipperReceivedTime: '揽件时间',
          shipperDeliveredTime: '送达/取消配送时间'
        }
      }
    },
    mounted() {
      // 获取门店
      // this.getStoreList()
      this.doSearchHandel(true)
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(_param) {
        this.searchContent.regionCode = _param.regionCode
        this.searchContent.cityId = _param.cityId
        this.searchContent.storeNo = _param.storeNo
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(reqUrls.search, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }

              this.storeOptions = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [doSearchHandel 搜索]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrls.orderDeliveryQuery, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.redirectUrl = `/order_mgt/detail/${item.orderId}`
              }
              this.tableData = res.data.result
            }else {
              this.tableData = []
            }

            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [showItem 查看配送日志]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showItem(_row) {
        this.orderNo = _row.orderId
        this.iconLoading = true
        this.$http.post(reqUrls.orderDeliveryLogQuery, { orderNo: this.orderNo, DIFFCITY: true }).then(res => {
          this.dialogVisible = true
          this.iconLoading = false
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.orderDeliveryLogQuery = res.data
            }else {
              this.orderDeliveryLogQuery = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.iconLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      exportHandel() {
        this.exportLoading = true
        this.$http.post(reqUrls.orderDeliveryQuery, { ...this.searchContent }).then(res => {
          this.exportLoading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              let tableDataArr = res.data.result
              const tHeader = utils.objectToArray(this.exportConfig, false)
              const filterVal = utils.objectToArray(this.exportConfig, true)
              exportSheet.ExportJsonToExcel({
                header: tHeader,
                filterVal,
                data: tableDataArr,
                filename: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_配送单`
              })
              this.$notify.success({
                title: '温馨提示',
                message: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_配送单导出成功`
              });
            }else {
              this.$notify.warning({
                title: '温馨提示',
                message: `没有查询到您想要的数据，导出数据为空！`
              });
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.beginTime = result ? result[0] : ''
        this.searchContent.endTime = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      datePickerRangeChangeDelivery(result) {
        this.searchContent.defaultRangeDelivery = result ? [...result] : result
        this.searchContent.deliveryBeginTime = result ? result[0] : ''
        this.searchContent.deliveryEndTime = result ? result[1] : ''
        this.doSearchHandel(true)
      }
    }
  }
</script>


